body,
html {
  height: 100%;
}
.contact_page {
  background-image: url("./media/contact_page.jpg");
  height: 83vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.papa_contact {
  margin: 0;
  position: absolute;
  background-color: rgba(20, 20, 20, 1);
  opacity: 1;
  top: 25%;
  left: 30%;
  right: 30%;
  bottom: 38%;
}

.texto_contact {
  background-color: none;
  opacity: 1;
  color: white;
}

.indento {
  text-indent: 30%;
}

.phone_icon {
  background-color: blue;
}
