body,
html {
  height: 100%;
}
.bg {
  background-image: url("./media/rhino_mainpage.jpg");
  height: 83vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.papa {
  margin: 0;
  position: absolute;
  background-color: none;
  opacity: 1;
  top: 30%;
  left: 30%;
  right: 30%;
  bottom: 45%;
}

.texto {
  background-color: none;
  opacity: 1;
  color: white;
}

.bimbo {
  background-color: rgba(116, 113, 113, 0.5);
  top: 30%;
  left: 30%;
  right: 30%;
  bottom: 45%;
}
