body,
html {
  height: 100%;
}
.aboutpage {
  background-image: url("./media/about_page.jpg");
  height: 83vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.papa_about {
  margin: 0;
  position: absolute;
  background-color: rgba(20, 20, 20, 0.8);
  opacity: 1;
  top: 30%;
  left: 30%;
  right: 30%;
  bottom: 40%;
}

.texto_about {
  background-color: none;
  opacity: 1;
  color: white;
}
